        <h4><b-img rounded="circle" class='border border-primary d-lg-none' src="@/assets/vite-together-logo-blue64x64.png" style="background-color: white;" width="64" alt="placeholder"></b-img>&nbsp;ViteTogether:</h4>     
          <div class="accordion" role="tablist">
