              <template #titlebar>
                What is ViteTogether?
              </template>
                <p class='lead'><strong>ViteTogether</strong> is a series of protocols for
                the Vite blockchain that allow for groups of
                users to collectively optimize their voting rewards.
                In essence, ViteTogether is a form of <strong>collective bargaining</strong>,
                where small Vite holders join together to negotiate
                with Vite whales to earn fair rewards for participation in the Vite's consensus mechanism.
    
                </p>
                <p> <i>Details:</i> In the <strong>Hierarchical Distributed Proof of Stake</strong> (HDPoS) consensus mechansism
                    in the Vite blockchain, all users can participate in consensus by <strong>voting</strong> for 
                    a <strong>Snapshot Block Producer</strong> (SBP). These SBPs are responsible for adding Snapshot Blocks to the Vite's
                    Snapshot Chain, which acts as a confirmation for all transactions occuring on the Vite network.
                </p>
                <p> As SBPs create new blocks, they directly earn block rewards from the network. Moreover, the more votes an SBP gets
                from users, the more block rewards they earn. Thus, SBPs are incentivized to attract voters by sharing some of the rewards.
                However, the negotiating power for small voters is minimal, and the amount of rewards shared is not always fair.
                <strong>ViteTogether</strong> solves this by providing a large mobile block of votes that votes for the SBP that provides
                the best rates of profit sharing.
                </p>
            </AccordionItem>
